/*------------------------------------------------------------
	Slider
------------------------------------------------------------*/
.c-slide{
	&__box{
		position: absolute;
		top: 20%;
		left: 0;
		width: 100%;
		display: flex;
		justify-content: flex-end;
	}
	&__inner{
		max-width: 375px;
		background-color: $color_main;
		padding: 2.8rem 4.7rem 2.3rem;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		border-radius: 18px;
		margin-right: 7rem;
	}
	&__ttl{
		color: $color_main02;
		line-height: 1.37;
		font-size: 2.4rem;
		font-weight: bold;
	}
	&__txt{
		font-size: 1.8rem;
		line-height: 1.38;
		color: $color2;
	}
	&__btn{
		background-color: $color_main02;
		color: $color_main;
		font-size: 1.4rem;
		font-weight: 700;
		line-height: 1.35;
		border-radius: 50px;
	}
}